import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { useUpdateEffect } from 'react-use';

import { normalizeRawUrl } from '@mssgme/helpers';
import { useEventCallback } from '@mssgme/hooks';
import { LandingsPropTypes, TextField, SelectField } from '@mssgme/ui';
import { isMatchedById } from '@mssgme/shared';
import { usePageData, useWebsiteData } from '@mssgme/userpage-core';
import { AnchorField } from '../AnchorField';
import { ActionPageSelector } from './ActionPageSelector';

const ALL_TYPES = ['url', 'page', 'tel', 'email', 'sms', 'fullImage', 'nothing', 'anchor'];
const TYPES = {
    all: ALL_TYPES,
    image: ALL_TYPES,
    link: ['url', 'page', 'tel', 'email', 'sms', 'anchor'],
};
const TEXT_INPUT_TYPES_MAP = {
    url: 'text',
    tel: 'tel',
    sms: 'tel',
    email: 'email',
};
const typeOptions = (t, blockKind) => {
    return TYPES[blockKind].map((value) => ({
        label: t(`ActionField.types.${value}`),
        value,
    }));
};

const urlLabelsMap = (t, type) => {
    return t(`ActionField.inputs.${type}`, { returnObjects: true });
};

export function ActionField({
    name,
    title,
    blockKind = 'all',
    actionLabel = '',
    urlLabel = '',
    required = false,
    ...rest
}) {
    const website = useWebsiteData();
    const page = usePageData();
    const { t } = useTranslation('admin-ui');
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value } = getFieldMeta(name);
    const type = value.type;
    const isPage = type === 'page';
    const isAnchor = type === 'anchor';
    const isUrl = type === 'url';
    const isTypeWithInput = type in TEXT_INPUT_TYPES_MAP;
    const options = useMemo(() => typeOptions(t, blockKind), [t, blockKind]);
    const actionValueLabels = useMemo(() => urlLabelsMap(t, type), [t, type]);
    const prevAnchor = value.anchor;
    const currentAnchor = useMemo(() => {
        if (type !== 'anchor') {
            return;
        }

        const pageFromValue = website.pages.find(isMatchedById(prevAnchor.page));
        const blockFromValue = pageFromValue?.blocks.some(isMatchedById(prevAnchor.block));

        if (!(pageFromValue && blockFromValue)) {
            return { page: page._id, block: page.blocks[0]._id };
        }
    }, [type, website.pages, value.anchor]);

    const handleNormalizeUrl = useEventCallback(() => {
        const url = normalizeRawUrl(value.url || '');

        if (url && url !== value?.url) {
            setFieldValue(`${name}.url`, url);
        }
    });

    useUpdateEffect(() => {
        setFieldValue(`${name}.url`, '');
        setFieldValue(`${name}.page`, null);
    }, [name, type]);

    useUpdateEffect(() => {
        if (!currentAnchor) {
            return;
        }

        setFieldValue(`${name}.anchor`, currentAnchor);
    }, [name, currentAnchor]);

    return (
        <div {...rest}>
            <SelectField
                builderInput
                required={required}
                name={`${name}.type`}
                label={actionLabel || t('ActionField.action')}
                options={options}
            />

            {isPage && (
                <ActionPageSelector
                    required={required}
                    website={website}
                    name={`${name}.page`}
                    label={actionValueLabels.label}
                    placeholder={actionValueLabels.placeholder}
                    title={title}
                />
            )}

            {isAnchor && (
                <AnchorField
                    builderInput
                    required={required}
                    name={`${name}.anchor`}
                    label={actionValueLabels.label}
                    placeholder={actionValueLabels.placeholder}
                    website={website}
                />
            )}

            {isTypeWithInput && (
                <TextField
                    builderInput
                    required={required}
                    autoCapitalize="off"
                    name={`${name}.url`}
                    label={urlLabel || actionValueLabels.label}
                    placeholder={actionValueLabels.placeholder}
                    onBlur={isUrl ? handleNormalizeUrl : undefined}
                    type={TEXT_INPUT_TYPES_MAP[type]}
                />
            )}
        </div>
    );
}

ActionField.propTypes = {
    name: PropTypes.string.isRequired,
    title: PropTypes.string,
    blockKind: PropTypes.oneOf(['all', 'link', 'image']),
    actionLabel: PropTypes.string,
    urlLabel: LandingsPropTypes.inputLabel,
    required: PropTypes.bool,
};
